hr {
   margin-left: 20vw;
   margin-right: 20vw;
}

a.nav-link {
   padding: .4rem;
   font-family: 'Quattrocento', serif;
   font-size: 1.2em;
}

.tab-content {
   padding-left: 1em;
   padding-right: 1em;
}

@media (min-width: 1000px) {
   a.nav-link {
      padding: 1rem;
      font-size: 1.5em;
   }
   div.tabs {
      text-align: center;
   }
}
